import lazyLoading from './lazyLoading'

export default {
  meta: {
    icon: 'fa-line-chart',
    expanded: false,
    label: 'Graph SMS'
  },

  children: [
    {
      name: 'Today',
      path: '/today',
      meta: {
        auth: true,
        link: 'graph/daily.vue'
      },
      component: lazyLoading('graph/daily')
    },
    {
      name: 'This Month',
      path: '/thismonth',
      meta: {
        auth: true,
        link: 'graph/monthly.vue'
      },
      component: lazyLoading('graph/monthly')
    }
  ]
}



