import lazyLoading from './lazyLoading'

export default {
  meta: {
    icon: 'fa-calendar',
    expanded: false,
    label: 'Statistic'
  },

  children: [
    {
      name: 'Daily',
      path: '/daily',
      meta: {
        auth: true,
        link: 'statistic/statDaily.vue'
      },
      component: lazyLoading('statistic/statDaily')
    },
    {
      name: 'Monthly',
      path: '/monthly',
      meta: {
        auth: true,
        link: 'statistic/statMonthly.vue'
      },
      component: lazyLoading('statistic/statMonthly')
    }
  ]
}



